import React from 'react';
import styled, { css } from 'styled-components';
import { today, strToDate } from '../../../../utils/utils';

const DeadlineWrapper = styled.div`
  margin: 10px 0 15px;
  padding-left: 3px;
  color: ${props => props.theme.layout.mainColor};
  ${props =>
    props.isOver &&
    css`
      color: ${props => props.theme.component.priority};
    `};
  ${props =>
    props.isFin &&
    css`
      color: ${props => props.theme.layout.outer};
    `};
  font-size: 0.7rem;
  font-weight: bold;
`;

/*
  1. ì‹œì‘ì¼ë§Œ ìˆëŠ” ê²½ìš°: ~ì— ì‹œì‘
  2. ë§ˆê°ì¼ë§Œ ìˆëŠ” ê²½ìš°
    a. ë§ˆê°ì¼ ì•ˆ ì§€ë‚œ ê²½ìš°: ~ì— ë§ˆê°
    b. ë§ˆê°ì¼ ì§€ë‚œ ê²½ìš°: ë§ˆê°ì¼ ~ì¼ ì§€ë‚¨
  3. ë‘˜ ë‹¤ ìˆëŠ” ê²½ìš°
    a. ë§ˆê°ì¼ ì•ˆ ì§€ë‚œ ê²½ìš°: ì‹œì‘ì¼ - ë§ˆê°ì¼
    b. ë§ˆê°ì¼ ì§€ë‚œ ê²½ìš°: ë§ˆê°ì¼ ~ì¼ ì§€ë‚¨
  4. ì™„ë£Œëœ ê²½ìš°: ~ì— ì™„ë£Œ
*/
const handleDeadline = (startDt, endDt, finDt) => {
  const start =
    startDt && `${startDt.substring(5, 7)}ì›”${startDt.substring(8)}ì¼`;
  const end = endDt && `${endDt.substring(5, 7)}ì›”${endDt.substring(8)}ì¼`;
  const fin = finDt && `${finDt.substring(5, 7)}ì›”${finDt.substring(8)}ì¼`;
  let result = '';

  if (fin) {
    return `${fin}ì— ì™„ë£Œ`;
  }
  if (end && today > endDt) {
    const diff = (strToDate(today) - strToDate(endDt)) / (1000 * 60 * 60 * 24);
    return `ë§ˆê°ì¼ ${diff}ì¼ ì§€ë‚¨`;
  }

  if (start && !end) {
    result = `${start}ì— ì‹œì‘`;
  } else if (!start && end) {
    result = `${end}ì— ë§ˆê°`;
  } else {
    result = `${start} - ${end}`;
  }
  return result;
};

const Deadline = ({ startDt, endDt, finDt }) => {
  const isOver = endDt && today > endDt ? true : false; // ê¸°í•œ ì§€ë‚¨ ì—¬ë¶€
  const isFin = finDt ? true : false; // ì™„ë£Œ ì—¬ë¶€
  return (
    <DeadlineWrapper isOver={isOver} isFin={isFin}>
      {handleDeadline(startDt, endDt, finDt)}
    </DeadlineWrapper>
  );
};

export default Deadline;
